import clsx from 'clsx';
import React, { useRef, useImperativeHandle, forwardRef } from 'react';
import { DividerDefaultProps, DividerPropTypes } from './generated/propTypes';

const Divider = forwardRef((props, externalRef) => {
    const {
        dividerColor,
        dividerColorImportant,
        dividerHeight,
        dividerStyle,
        themeColor,
        themeColorImportant,
        variant,
        className,
        ...customProps
    } = props;

    // Supporting deprecated props
    const currentThemeColorImportant =
        dividerColorImportant || themeColorImportant;
    const currentVariant = dividerHeight || variant;
    const currentThemeColor = dividerColor || themeColor;

    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    const classNames = clsx(
        'lmnt',
        'lmnt-divider',
        currentThemeColorImportant && 'lmnt-divider--color-important',
        currentVariant === 'dense' && 'lmnt-divider--dense',
        currentVariant === 'tall' && 'lmnt-divider--tall',
        dividerStyle === 'dotted' && 'lmnt-divider--dotted',
        dividerStyle === 'dashed' && 'lmnt-divider--dashed',
        dividerStyle === 'solid' && 'lmnt-divider--solid',
        dividerStyle === 'double' && 'lmnt-divider--double',
        dividerStyle === 'groove' && 'lmnt-divider--groove',
        dividerStyle === 'ridge' && 'lmnt-divider--ridge',
        dividerStyle === 'inset' && 'lmnt-divider--inset',
        dividerStyle === 'outset' && 'lmnt-divider--outset',
        currentThemeColor === 'primary' && 'lmnt-divider--primary',
        currentThemeColor === 'primary-variant' &&
            'lmnt-divider--primary-variant',
        currentThemeColor === 'primary-50' && 'lmnt-divider--primary-50',
        currentThemeColor === 'primary-100' && 'lmnt-divider--primary-100',
        currentThemeColor === 'primary-200' && 'lmnt-divider--primary-200',
        currentThemeColor === 'primary-300' && 'lmnt-divider--primary-300',
        currentThemeColor === 'primary-400' && 'lmnt-divider--primary-400',
        currentThemeColor === 'primary-500' && 'lmnt-divider--primary-500',
        currentThemeColor === 'primary-600' && 'lmnt-divider--primary-600',
        currentThemeColor === 'primary-700' && 'lmnt-divider--primary-700',
        currentThemeColor === 'primary-800' && 'lmnt-divider--primary-800',
        currentThemeColor === 'primary-900' && 'lmnt-divider--primary-900',
        currentThemeColor === 'secondary' && 'lmnt-divider--secondary',
        currentThemeColor === 'secondary-variant' &&
            'lmnt-divider--secondary-variant',
        currentThemeColor === 'secondary-50' && 'lmnt-divider--secondary-50',
        currentThemeColor === 'secondary-100' && 'lmnt-divider--secondary-100',
        currentThemeColor === 'secondary-200' && 'lmnt-divider--secondary-200',
        currentThemeColor === 'secondary-300' && 'lmnt-divider--secondary-300',
        currentThemeColor === 'secondary-400' && 'lmnt-divider--secondary-400',
        currentThemeColor === 'secondary-500' && 'lmnt-divider--secondary-500',
        currentThemeColor === 'secondary-600' && 'lmnt-divider--secondary-600',
        currentThemeColor === 'secondary-700' && 'lmnt-divider--secondary-700',
        currentThemeColor === 'secondary-800' && 'lmnt-divider--secondary-800',
        currentThemeColor === 'secondary-900' && 'lmnt-divider--secondary-900',
        currentThemeColor === 'danger' && 'lmnt-divider--danger',
        currentThemeColor === 'danger-variant' &&
            'lmnt-divider--danger-variant',
        currentThemeColor === 'danger-50' && 'lmnt-divider--danger-50',
        currentThemeColor === 'danger-100' && 'lmnt-divider--danger-100',
        currentThemeColor === 'danger-200' && 'lmnt-divider--danger-200',
        currentThemeColor === 'danger-300' && 'lmnt-divider--danger-300',
        currentThemeColor === 'danger-400' && 'lmnt-divider--danger-400',
        currentThemeColor === 'danger-500' && 'lmnt-divider--danger-500',
        currentThemeColor === 'danger-600' && 'lmnt-divider--danger-600',
        currentThemeColor === 'danger-700' && 'lmnt-divider--danger-700',
        currentThemeColor === 'danger-800' && 'lmnt-divider--danger-800',
        currentThemeColor === 'danger-900' && 'lmnt-divider--danger-900',
        currentThemeColor === 'ancillary-100' && 'lmnt-divider--ancillary-100',
        currentThemeColor === 'ancillary-200' && 'lmnt-divider--ancillary-200',
        currentThemeColor === 'ancillary-300' && 'lmnt-divider--ancillary-300',
        currentThemeColor === 'ancillary-400' && 'lmnt-divider--ancillary-400',
        currentThemeColor === 'ancillary-500' && 'lmnt-divider--ancillary-500',
        currentThemeColor === 'ancillary-600' && 'lmnt-divider--ancillary-600',
        currentThemeColor === 'ancillary-700' && 'lmnt-divider--ancillary-700',
        currentThemeColor === 'ancillary-800' && 'lmnt-divider--ancillary-800',
        currentThemeColor === 'ancillary-900' && 'lmnt-divider--ancillary-900',
        currentThemeColor === 'on-surface-stroke' &&
            'lmnt-theme-on-surface-stroke',
        className
    );

    return <hr ref={ref} className={classNames} {...customProps} />;
});

Divider.displayName = 'Divider';
Divider.propTypes = DividerPropTypes;
Divider.defaultProps = DividerDefaultProps;

export default Divider;
